// paging
.paging {
  display: box;
  display: -webkit-box;
  display: -moz-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: -moz-flex;
  display: -o-flex;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 20px 0 20px 0;
  a {
    display: box;
    display: -webkit-box;
    display: -moz-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: -moz-flex;
    display: -o-flex;
    display: flex;
    justify-content: center;
    width: 30px;
    height: 30px;
    margin: 0 2px 0px 2px;
    line-height: 28px;
    text-align: center;
    font-size: 16px;
    color: #666;
    font-weight: normal;
    border: 1px solid #fff;
    -webkit-border-radius: 3px;
    -ms-border-radius: 3px;
    -moz-border-radius: 3px;
    -o-border-radius: 3px;
    border-radius: 3px;
    transition: all 0.3s;
    font-family: 'NotoSansLight';
    &:hover,
    &.active {
      background-color: #fff;
      border-width: 1px;
      border-color: #ec008c;
      color: #ec008c;
      font-family: 'NotoSansMedium';
    }
  }
  span {
    display: box;
    display: -webkit-box;
    display: -moz-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: -moz-flex;
    display: -o-flex;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 40px;
    height: 40px;
  }
  .btn_arrow {
    position: relative;
    background-color: #fff;
    border: 1px solid #cccccc;
    color: #323232;
    font-size: 0;
    margin: 0 2px 0 2px;
    &::before {
      display: block;
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      width: 5px;
      height: 9px;
      background: url('../images/common/icon_paging.png') repeat-x left center;
      margin-left: -3px;
      margin-top: -4px;
    }
    &.prev {
      margin-right: 16px;
    }
    &.next {
      margin-left: 16px;
      &::before {
        transform: rotate(180deg);
      }
    }
    &.first {
      &::before {
        margin-left: -5px;
        width: 10px;
      }
    }
    &.last {
      &::before {
        margin-left: -5px;
        width: 10px;
        transform: rotate(180deg);
      }
    }
  }
}

// radio button
.custom_radio {
  vertical-align: middle;
  display: inline-block;
  position: relative;
  & + .custom_radio {
    margin-left: 30px;
  }
  input[type='radio'] {
    display: block;
    position: absolute;
    top: -1px;
    left: -1px;
    width: 0;
    height: 0;
    opacity: 0;
    & + label {
      position: relative;
      padding-left: 30px;
      font-size: 16px;
      color: #666;
      font-weight: normal;
      cursor: pointer;
      &::before {
        display: block;
        content: '';
        position: absolute;
        top: 2px;
        left: 0;
        z-index: 1;
        width: 20px;
        height: 20px;
        box-sizing: border-box;
        border: 1px solid #cdcdcd;
        border-radius: 50%;
        background-color: #fff;
      }
    }
    &:checked {
      & + label {
        &::before {
          border-color: #ec008c;
        }
        &::after {
          display: block;
          content: '';
          position: absolute;
          top: 8px;
          left: 6px;
          z-index: 1;
          width: 8px;
          height: 8px;
          box-sizing: border-box;
          border-radius: 50%;
          background-color: #ec008c;
        }
      }
      &:disabled {
        & + label {
          &::before {
            border-color: #cdcdcd;
          }
          &::after {
            background-color: #cdcdcd;
          }
        }
      }
    }
    &:disabled {
      & + label {
        &::before {
          background: #f6f6f6;
        }
      }
    }
  }
  &.icon_type {
    text-align: center;
    input[type='radio'] {
      & + label {
        display: inline-flex;
        &::before {
          top: -1px;
        }
        &::after {
          top: 5px;
        }
        .txt {
          text-align: left;
        }
      }
    }
    &::before {
      content: '';
      display: block;
      width: 80px;
      aspect-ratio: 80/120;
      margin: 0 auto 15px;
    }
    &.sms {
      &::before {
        background: url('../images/icon/icon_rdo_sms.png') no-repeat 0 0;
        background-size: 100% auto;
      }
    }
    &.lms {
      &::before {
        background: url('../images/icon/icon_rdo_lms.png') no-repeat 0 0;
        background-size: 100% auto;
      }
    }
    &.rcs_tmp1 {
      &::before {
        background: url('../images/icon/icon_rdo_rcs_tmp1.png') no-repeat 0 0;
        background-size: 100% auto;
      }
    }
    &.rcs_tmp2 {
      &::before {
        background: url('../images/icon/icon_rdo_rcs_tmp2.png') no-repeat 0 0;
        background-size: 100% auto;
      }
    }
    &.rcs_tmp3 {
      &::before {
        background: url('../images/icon/icon_rdo_rcs_tmp3.png') no-repeat 0 0;
        background-size: 100% auto;
      }
    }
    &.rcs_tmp4 {
      &::before {
        background: url('../images/icon/icon_rdo_rcs_tmp4.png') no-repeat 0 0;
        background-size: 100% auto;
      }
    }
    &.rcs_tmp5 {
      &::before {
        background: url('../images/icon/icon_rdo_rcs_tmp5.png') no-repeat 0 0;
        background-size: 100% auto;
      }
    }
    &.rcs_tmp6 {
