// checkbox
.custom_checkbox {
  vertical-align: middle;
  display: inline-block;
  position: relative;
  & + .custom_checkbox {
    margin-left: 30px;
  }
  input[type='checkbox'] {
    display: block;
    position: absolute;
    top: -1px;
    left: -1px;
    width: 0;
    height: 0;
    opacity: 0;
    & + label {
      position: relative;
      padding-left: 30px;
      font-size: 16px;
      color: #666;
      font-weight: normal;
      cursor: pointer;
      line-height: 1.625;
      &::before {
        display: block;
        content: '';
        position: absolute;
        top: 2px;
        left: 0;
        z-index: 1;
        width: 20px;
        height: 20px;
        box-sizing: border-box;
        border: 1px solid #cdcdcd;
        background-color: #fff;
        border-radius: 2px;
      }
    }
    &:checked {
      & + label {
        &::before {
          border-color: #ec008c;
          background: url('../images/common/icon_checkbox.png') no-repeat center #ec008c;
        }
      }
      &:disabled {
        & + label {
          &::before {
            border-color: #cdcdcd;
            background: url('../images/common/icon_checkbox.png') no-repeat center #cdcdcd;
          }
        }
      }
    }
    &:disabled {
      & + label {
        &::before {
          background-color: #cdcdcd;
        }
      }
    }
  }
  &.fs14 {
    input[type='checkbox'] {
      & + label {
        font-size: 14px;
      }
      &:disabled {
        & + label {
          &::before {
            background-color: #cdcdcd;
          }
        }
      }
    }
  }
}

