// input
.custom_input {
  vertical-align: middle;
  display: inline-block;
  position: relative;
  input[type='text'],
  input[type='password'] {
    width: 100%;
    height: 40px;
    box-sizing: border-box;
    padding: 0 20px;
    background-color: #fff;
    border: 1px solid #cccccc;
    font-size: 16px;
    color: #666;
    font-weight: normal;
    letter-spacing: inherit;
  }
  input[readonly],
  input[disabled] {
    background-color: #f6f6f6;
    &:focus {
      border-color: #ccc;
    }
  }
  input {
    &:focus {
      border-color: #666;
    }
  }
  &.big {
    input {
      height: 60px;
    }
  }
  &.icon_date {
    input[type='text'] {
      background-color: #fff;
      background-image: url('../images/common/icon_date.png');
      background-repeat: no-repeat;
      background-position: calc(100% - 10px) 50%;
      &:focus {
        border-color: #cccccc;
        background-color: #f6f6f6;
        color: #898989;
      }
    }
  }
  &.length {
    input {
      padding-right: 50px;
    }
  }
  .length_count {
    position: absolute;
    top: 50%;
    right: 10px;
    transform: translate(0, -50%);
    font-size: 16px;
    color: #cccc;
    font-weight: normal;
    font-family: 'NotoSansLight';
    z-index: 2;
    em {
      font-style: normal;
    }
    .count {
      &.active {
        color: #666;
      }
    }
  }
  &.search {
    position: relative;
    input {
      padding-right: 40px;
    }
    .btn_search {
      display: block;
      position: absolute;
      top: 0;
      right: 0;
      width: 40px;
      height: 100%;
      background: url('../images/common/icon_search.png') no-repeat 50% 50%;
      font-size: 0;
    }
  }
}
