// accordion
.accordion_wrap {
  position: relative;
  .accor_bundle_list {
    margin: 0;
    padding: 0;
    list-style-type: none;
    & > li {
      position: relative;
      margin-top: 10px;
      border: 1px solid #ddd;
      .header_accordion {
        & > .btn_accordion {
          position: relative;
          display: box;
          display: -webkit-box;
          display: -moz-box;
          display: -webkit-flex;
          display: -ms-flexbox;
          display: -moz-flex;
          display: -o-flex;
          display: flex;
          justify-content: flex-start;
          align-items: center;
          height: 50px;
          box-sizing: border-box;
          padding: 0 30px;
          background-color: #f7f7f7;
          &::after {
            display: block;
            content: '';
            position: absolute;
            top: 50%;
            right: 30px;
            width: 18px;
            height: 10px;
            box-sizing: border-box;
            background: url('../images/common/icon_toggle_arrow.png') no-repeat 50% 50%;
            transform: translateY(-50%);
          }
          &.active {
            &::after {
              transform: translateY(-50%) rotate(180deg);
            }
          }
        }
      }
      .content_accordion {
        padding: 0 30px;
        display: none;
        &.active {
          display: block;
        }
        .item_bundle {
          .tit_bundle {
            width: 110px;
          }
          .cont_bundle {
            width: calc(100% - 120px);
          }
        }
      }
    }
  }
}

// tooltip
.tooltip_area {
  vertical-align: middle;
  display: inline-block;
  position: relative;
  margin-left: 6px;
  font-size: 0;
  z-index: 10;
  .btn_info {
    position: relative;
    vertical-align: middle;
    display: inline-block;
    z-index: 3;
    width: 20px;
    height: 20px;
    box-sizing: border-box;
    font-size: 16px;
    color: #898989;
    font-weight: normal;
    &::before {
      display: block;
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 20px;
      height: 20px;
      background: url('../images/common/icon_guide.png') no-repeat 50% 50%;
    }
    &:hover {
      & + .box_tooltip {
        display: block;
      }
    }
  }
  .box_tooltip {
    display: none;
    position: absolute;
    top: -5px;
    left: 32px;
    z-index: 5;
    padding: 16px;
    background-color: #686868;
    text-align: left;
    box-sizing: border-box;
    font-size: 14px;
    color: #fff;
    font-weight: normal;
    line-height: 1.3;
    word-break: keep-all;
    -webkit-border-radius: 4px;
    -ms-border-radius: 4px;
    -moz-border-radius: 4px;
    -o-border-radius: 4px;
    border-radius: 4px;
    letter-spacing: -0.03em;
    white-space: nowrap;
    &::before {
      vertical-align: middle;
      display: inline-block;
      content: '';
      position: absolute;
      top: 10px;
      left: -5px;
      z-index: 2;
      width: 10px;
      height: 10px;
      box-sizing: border-box;
      background-color: #686868;
      transform: rotate(45deg);
    }
    .title {
      margin-bottom: 15px;
      font-size: 14px;
      color: #333;
      font-weight: normal;
    }
    .bullet_list {
      li {
        &.text_blue {
          color: #2a60e4;
        }
      }
    }
    ul {
      &.tooltip_list {
        li {
          position: relative;
          margin-left: 10px;
          text-indent: -10px;
          line-height: 150%;
          border: 0;
          margin-top: 0;
          &::before {
            vertical-align: middle;
            display: inline-block;
            content: '';
            width: 3px;
            height: 3px;
            margin-right: 5px;
            box-sizing: border-box;
            background-color: #fff;
          }
          span {
            border-bottom: 1px solid #fff;
          }
        }
      }
    }
  }
  .text_info {
    position: relative;
    display: block;
    font-size: 16px;
    color: #666;
    font-weight: normal;
    font-family: 'NotoSansLight';
    &.fileName {
      width: 180px;
    }
    &.fileID {
      width: 270px;
    }
    &:hover {
      & + .box_text_tooltip {
        display: block;
      }
    }
  }
  &.bullet_tooltip_area {
    .box_tooltip {
      min-width: 200px;
      top: 25px;
      right: -170px;
      left: auto;
      padding: 10px;
      &::before {
        top: -5px;
        right: 175px;
        left: auto;
      }
    }
    th,
    td {
      padding: 3px 7px;
      text-align: center;
      border: 1px solid #fff;
    }
    tbody {
      th {
        text-align: left;
      }
    }
  }
}

