          & + label {
            padding: 0;
            color: #aaa;
            line-height: inherit;
            &::before {
              display: none;
            }
          }
          &:checked {
            & + label {
              color: #222;
              &::before {
                display: none;
              }
              &::after {
                display: none;
              }
            }
          }
        }
        & + .custom_radio {
          margin-left: 10px;
        }
        &.sml {
          input[type='radio'] {
            & + label {
              font-size: 10px;
            }
          }
        }
        &.mid {
          input[type='radio'] {
            & + label {
              font-size: 12px;
            }
          }
        }
        &.big {
          input[type='radio'] {
            & + label {
              font-size: 14px;
            }
          }
        }
      }
    }
  }
}

