.btn {
  position: relative;
  vertical-align: middle;
  display: inline-block;
  border: 1px solid #222;
  background-color: #fff;
  text-align: center;
  transition: all 0.3s;
  font-family: 'NotoSansMedium';
  color: #222;
  border-radius: 3px;
  & + .btn {
    margin-left: 8px;
  }
  &:hover {
    background-color: #222;
    color: #fff;
    .box_tooltip {
      display: block;
    }
  }
  & > span {
    position: relative;
    z-index: 2;
  }
  /* button color */
  &.gray {
    border-color: #777;
    background-color: #777;
    color: #fff;
    &:hover {
      border-color: #444;
      background-color: #444;
    }
  }
  &.light_gray {
    border-color: #949dc7;
    background-color: #949dc7;
    color: #fff;
    &:hover {
      border-color: #bdc4e2;
      background-color: #bdc4e2;
    }
  }
  &.bd_gray {
    border: 1px solid #999;
    color: #333;
    &:hover {
      background-color: #f0f0f0;
    }
  }
  &.black {
    border-color: #444444;
    background-color: #444444;
    color: #fff;
    &:hover {
      background-color: #222;
    }
    &.icon_plus {
      span {
        &::before,
        &::after {
          background-color: #fff;
        }
      }
    }
  }
  &.bd_black {
    border: 1px solid #222;
    background-color: #fff;
    color: #222;
    &:hover {
      background-color: #222;
      color: #fff;
    }
  }
  &.point {
    border: 1px solid #ec008c;
    background-color: #fff;
    color: #ec008c;
    &:hover {
      background-color: #ec008c;
      color: #fff;
    }
    &.on {
      background-color: #ec008c;
      color: #fff;
      &:hover {
        background-color: #ff0097;
        color: #fff;
      }
    }
    .box_tooltip {
      top: 50px;
      left: 45px;
      p {
        margin: 0;
        color: #fff;
      }
    }
  }
  &.square {
    -webkit-border-radius: 0;
    -ms-border-radius: 0;
    -moz-border-radius: 0;
    -o-border-radius: 0;
    border-radius: 0;
    &.mid {
      min-width: 100px;
      font-size: 14px;
      padding: 0 20px;
    }
  }
  /* size */
  &.sml {
    height: 32px;
    box-sizing: border-box;
    padding: 0 16px;
    font-size: 14px;
    line-height: 30px;
    &.btn_pop {
      padding: 0;
    }
  }
  &.mid {
    min-width: 120px;
    height: 40px;
    box-sizing: border-box;
    padding: 0 30px 0 30px;
    font-size: 16px;
    line-height: 38px;
  }
  &.big {
    min-width: 130px;
    height: 50px;
    box-sizing: border-box;
    padding: 0 20px 0 20px;
    font-size: 18px;
    line-height: 48px;
  }
  &.btn_pop {
    & > span {
      display: block;
      width: 100%;
      height: 100%;
      box-sizing: border-box;
      padding: 0 16px;
    }
  }
  /* select */
  &.select {
    width: 80px;
    border-color: #999999;
    &:hover {
      border: 1px solid #ec008c;
      background-color: #ec008c;
      color: #fff;
    }
  }
  &.icon_plus {
    span {
      position: relative;
      padding-right: 15px;
      &::before,
      &::after {
        display: block;
        content: '';
        position: absolute;
        top: 50%;
        right: 0;
        width: 8px;
        height: 2px;
        margin-left: -4px;
        margin-top: -1px;
      }
      &::after {
        transform: rotate(90deg);
      }
    }
  }
  /* btn etc */
  &_link {
    display: inline-block;
    position: relative;
    padding-right: 13px;
    font-size: 16px;
    color: #666666;
    font-weight: normal;
    line-height: 1;
    &::after {
      display: block;
      content: '';
      position: absolute;
      top: 50%;
      right: 0;
      width: 7px;
      height: 7px;
      box-sizing: border-box;
      border-bottom: 2px solid #898989;
      border-right: 2px solid #898989;
      transform: translate(0, -50%) rotate(-45deg);
    }
    .box_tooltip {
      display: none;
      position: absolute;
      top: 25px;
      left: 45px;
      z-index: 5;
      padding: 16px;
      background-color: #686868;
      text-align: left;
      box-sizing: border-box;
      font-size: 14px;
      color: #fff;
      font-weight: normal;
      line-height: 1.3;
      word-break: keep-all;
      -webkit-border-radius: 4px;
      -ms-border-radius: 4px;
      -moz-border-radius: 4px;
      -o-border-radius: 4px;
      border-radius: 4px;
      letter-spacing: -0.03em;
      white-space: nowrap;
      &::before {
        vertical-align: middle;
        display: inline-block;
        content: '';
        position: absolute;
        top: -5px;
        left: 17px;
        z-index: 2;
        width: 10px;
        height: 10px;
        box-sizing: border-box;
        background-color: #686868;
        transform: rotate(45deg);
      }
      p {
        margin: 0;
        color: #fff;
      }
      &:hover {
        .box_tooltip {
          display: block;
        }
      }
    }
  }
  &_guide {
    vertical-align: middle;
    display: inline-block;
    position: relative;
    font-size: 16px;
    color: #898989;
    font-weight: normal;
    line-height: 1;
    text-align: center;
    span {
      position: relative;
      padding-right: 25px;
      &::after {
        display: block;
        content: '';
        position: absolute;
        top: 50%;
        right: 0;
        width: 20px;
        height: 20px;
        margin-top: -10px;
        background: url('../images/common/icon_guide.png') no-repeat 50% 50%;
        -webkit-border-radius: 50%;
        -ms-border-radius: 50%;
        -moz-border-radius: 50%;
        -o-border-radius: 50%;
        border-radius: 50%;
        line-height: 18px;
        font-size: 14px;
      }
    }
  }
  &_delete {
    position: relative;
    vertical-align: middle;
    display: inline-block;
    padding-right: 22px;
    font-size: 16px;
    color: #898989;
    font-weight: normal;
    &::before,
    &::after {
      display: block;
      position: absolute;
      top: 50%;
      right: 0;
      content: '';
      width: 18px;
      height: 1px;
      background-color: #797979;
    }
    &::before {
      transform: translate(0, -50%) rotate(45deg);
    }
    &::after {
      transform: translate(0, -50%) rotate(-45deg);
    }
  }
  &_preview {
    vertical-align: middle;
    display: inline-block;
    width: 30px;
    height: 30px;
    box-sizing: border-box;
    background: url('../images/common/icon_preview.png') no-repeat 50% 50%;
    font-size: 0;
  }
  &_text {
    &:hover {
      text-decoration: underline;
    }
  }
  .box_tooltip {
    display: none;
    position: absolute;
    top: 40px;
    left: 23px;
    z-index: 5;
    padding: 16px;
    background-color: #686868;
    text-align: left;
    box-sizing: border-box;
    font-size: 14px;
    color: #fff;
    font-weight: normal;
    line-height: 1.3;
    word-break: keep-all;
    -webkit-border-radius: 4px;
    -ms-border-radius: 4px;
    -moz-border-radius: 4px;
    -o-border-radius: 4px;
    border-radius: 4px;
    letter-spacing: -0.03em;
    white-space: nowrap;
    &::before {
      vertical-align: middle;
      display: inline-block;
      content: '';
      position: absolute;
      top: -5px;
      left: 17px;
      z-index: 2;
      width: 10px;
      height: 10px;
      box-sizing: border-box;
      background-color: #686868;
      transform: rotate(45deg);
    }
    p {
      margin: 0;
      color: #fff;
    }
  }
  &.disabled {
    pointer-events: none;
    opacity: 0.6;
  }
}

.btn_wrap {
  &.row {
    .btn {
      display: block;
      & + .btn {
        margin-left: 0;
        margin-top: 10px;
      }
    }
  }
  &.lookup {
    display: flex;
    align-items: center;
    gap: 0 10px;
    width: 100%;
    .btn {
      flex-shrink: 0;
    }
    .txt {
      // overflow: hidden;
      // text-overflow: ellipsis;
      // white-space: nowrap;
      max-width: 100%;
    }
  }
}
