// layer popup
.layer {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 99;
  display: none;
	&.active {
		display: block;
	}
	&_cont {
		position: absolute;
		top: 50%;
		left: 50%;
		width: 900px;
		transform: translate(-50%, -50%);
		background-color: #fff;
		-webkit-border-radius: 20px;
		-ms-border-radius: 20px;
		-moz-border-radius: 20px;
		-o-border-radius: 20px;
		border-radius: 20px;
		overflow: hidden;
		box-sizing: border-box;
		&.wid_layer_cont {
			width: 1100px;
		}
		/*오류 다운로드 팝업*/
		&.error {
			width: 500px;
			.layer_body {
				margin: 0;
				margin-bottom: 20px;
				padding-top: 50px;
				.box_result_info {
					padding: 23px 30px 23px 30px;
					background-color: #f0f0f0;
				}
				.cont_flex_bundle {
					justify-content: space-between;
					.h4_title {
						color: #222;
					}
					.h4_desc {
						color: #333;
						font-family: "NotoSansMedium";
						.btn_down {
							display: inline-block;
							vertical-align: middle;
							width: 18px;
							height: 18px;
							margin-left: 4px;
							font-size: 0;
							background: url("../images/common/icon_file_down.png") no-repeat 50% 50%;
							background-size: contain;
						}
					}
				}
			}
			.btn{
				&.sml {
					width: 120px;
				}
			}
			.red {
				color: #f00000;
			}
		}
		&.alert{
			width: 520px !important;
			.layer_body{
				margin: 0;
				padding: 30px;
				.txt_set{
					margin: 20px 0 40px;
					@include font(16px, #666);
					line-height: 1.6;
					text-align: center;
				}
			}
		}
	}
	&_head {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 70px;
		box-sizing: border-box;
		padding: 20px 50px 20px 50px;
		border-bottom: 1px solid #dddddd;
		h2 {
			font-size: 22px;
			color: #222222;
			font-weight: normal;
			font-family: "NotoSansBold";
		}
	}
	&_body {
		margin: 70px 0 30px 0;
		padding: 30px 50px 30px 50px;
		max-height: 70vh;
		box-sizing: border-box;
		overflow-y: auto;
		.title_area {
			margin-bottom: 20px;
			padding: 0 0;
		}
		.tab_wrap {
			margin-top: 30px;
		}
	}
	&_sec{
		& + .layer_sec {
			margin-top: 35px;
		}
	}
	.btn_close {
		display: block;
		width: 50px;
		height: 50px;
		color: #fff;
		position: absolute;
		top: 10px;
		right: 40px;
		font-size: 0;
		text-align: center;
		line-height: 50px;
		&::before,
		&::after {
			display: block;
			content: '';
			position: absolute;
			top: 50%;
			left: 50%;
			width: 32px;
			height: 1px;
			background-color: #222222;
		}
		&::before {
			transform: translate(-50%, -50%) rotate(45deg);
		}
		&::after {
			transform: translate(-50%, -50%) rotate(-45deg);
		}
	}
	.layer_scroll {
		max-height: 45vh;
		overflow-y: auto;
	}
	&.mid{
		.layer_cont {
			width: 800px;
			height: auto;
		}
		.layer_body {
			height: auto;
		}
	}
	.tbl_col_type{
		thead{
			th {
				padding: 0 8px 0 8px;
			}
		}
		tbody{
			tr{
				td{
					&.no_data {
						height: 130px;
					}
				}
			}
		}
	}
	// 템플릿 가이드 팝업
	&.template_guide{
		.title_area {
			margin-bottom: 15px;
		}
	}
	// 브랜드 key 팝업
	&.brand_key{
		.layer_cont {
			height: auto;
		}
	}
	// 메시지 발송 팝업
	&.send_message{
		.cont_bundle{
			.text_info {
				display: box;
				display: -webkit-box;
				display: -moz-box;
				display: -webkit-flex;
				display: -ms-flexbox;
				display: -moz-flex;
				display: -o-flex;
				display: flex;
				justify-content: space-between;
				em {
					font-style: normal;
				}
			}
		}
	}
}

// loading_layer
.loading_layer {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: transparent;
  z-index: 99;
	.loader {
		display: block;
		position: absolute;
		top: 50%;
		left: 50%;
		width: 150px;
		height: 150px;
		transform: translate(-50%, -50%);
		-webkit-border-radius: 8px;
		-ms-border-radius: 8px;
		-moz-border-radius: 8px;
		-o-border-radius: 8px;
		border-radius: 8px;
		box-shadow: 0px 60px 120px 0px rgba(0, 0, 0, 0.25);
		background: #fff url("../images/common/loading.gif") no-repeat 50% 50%;
	}
}