// header
.header_wrap {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100px;
  box-sizing: border-box;
  background-color: #ffffff;
  border-bottom: 1px solid #ddd;
  z-index: 20;
  overflow: hidden;
  transition: all 0.3s;
  &.active {
    overflow: visible;
    border: 0;
  }
  &::before {
    display: block;
    content: '';
    position: absolute;
    top: 99px;
    left: 0;
    width: 100%;
    height: 1px;
    background-color: #ddd;
  }
  .header {
    position: relative;
    display: box;
    display: -webkit-box;
    display: -moz-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: -moz-flex;
    display: -o-flex;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-left: 243px;
    width: 1080px;
    margin: 0 auto;
    box-sizing: border-box;
    .logo {
      display: box;
      display: -webkit-box;
      display: -moz-box;
      display: -webkit-flex;
      display: -ms-flexbox;
      display: -moz-flex;
      display: -o-flex;
      display: flex;
      align-items: center;
      position: absolute;
      top: 0;
      left: 0;
      width: 243px;
      height: 100px;
    }
  }
  nav {
    /*width: 100%;*/
    width: 87%;
    margin-right: 13%;
    & > ul {
      display: box;
      display: -webkit-box;
      display: -moz-box;
      display: -webkit-flex;
      display: -ms-flexbox;
      display: -moz-flex;
      display: -o-flex;
      display: flex;
      height: 100%;
      box-sizing: border-box;
      & > li {
        &.node1 {
          position: relative;
          & > a {
            display: box;
            display: -webkit-box;
            display: -moz-box;
            display: -webkit-flex;
            display: -ms-flexbox;
            display: -moz-flex;
            display: -o-flex;
            display: flex;
            position: relative;
            width: 145px;
            height: 100px;
            box-sizing: border-box;
            justify-content: center;
            align-items: center;
            font-size: 18px;
            color: #222222;
            font-weight: normal;
            font-family: 'LGSmHaTSB';
            &:hover {
              color: #ec008c;
              &::before {
                transform: scaleX(1);
              }
            }
            &::before {
              display: block;
              content: '';
              position: absolute;
              bottom: -1px;
              left: 50%;
              width: 94px;
              height: 3px;
              margin-left: -47px;
              background-color: #ec008c;
              transform: scaleX(0);
              transition: all 0.3s;
            }
          }
        }
      }
    }
  }
  .sub_menu {
    padding: 40px 0 50px 0;
    & > li {
      &.node2 {
        & > a {
          position: relative;
          display: block;
          font-size: 16px;
          color: #666;
          font-weight: normal;
          font-family: 'LGSmHaTR';
          text-align: center;
          &:hover {
            font-family: 'LGSmHaTSB';
            color: #ec008c;
            & > .box_tooltip {
              display: block;
            }
          }
        }
        & + li.node2 {
          margin-top: 16px;
        }
      }
    }
  }
  .util_wrap {
    display: box;
    display: -webkit-box;
    display: -moz-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: -moz-flex;
    display: -o-flex;
    display: flex;
    align-items: center;
    position: absolute;
    top: 0;
    right: 0;
    height: 100px;
    box-sizing: border-box;
    .btn_user {
      display: block;
      width: 40px;
      height: 40px;
      -webkit-border-radius: 50%;
      -ms-border-radius: 50%;
      -moz-border-radius: 50%;
      -o-border-radius: 50%;
      border-radius: 50%;
      background: url('../images/common/icon_user.png') no-repeat center center #f6f6f6;
      font-size: 0;
      transition: all 0.3s;
      &.logout {
        background-image: url('../images/common/icon_logout.png');
        background-position: 12px 50%;
      }
    }
  }
  .empty_wrap {
    display: box;
    display: -webkit-box;
    display: -moz-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: -moz-flex;
    display: -o-flex;
    display: flex;
    align-items: center;
    position: absolute;
    top: 0;
    right: 40px;
    height: 100px;
    box-sizing: border-box;
    .emptyspace {
      display: block;
      width: 100px;
      height: 40px;
    }
  }
  .box_tooltip {
    display: none;
    position: absolute;
    top: 24px;
    left: 74px;
    z-index: 5;
    padding: 16px;
    background-color: #686868;
    text-align: left;
    box-sizing: border-box;
    font-size: 14px;
    color: #fff;
    font-weight: normal;
    line-height: 1.3;
    word-break: keep-all;
    -webkit-border-radius: 4px;
    -ms-border-radius: 4px;
    -moz-border-radius: 4px;
    -o-border-radius: 4px;
    border-radius: 4px;
    letter-spacing: -0.03em;
    white-space: nowrap;
    &::before {
      vertical-align: middle;
      display: inline-block;
      content: '';
      position: absolute;
      top: -5px;
      left: 17px;
      z-index: 2;
      width: 10px;
      height: 10px;
      box-sizing: border-box;
      background-color: #686868;
      transform: rotate(45deg);
    }
    .title {
      margin-bottom: 15px;
      font-size: 14px;
      color: #333;
      font-weight: normal;
    }
    .bullet_list {
      li {
        &.text_blue {
          color: #2a60e4;
        }
      }
    }
  }
}
.header_dimm {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  background-color: rgba(0, 0, 0, 0.5);
  transition: all 0.3s;
  opacity: 0;
  z-index: 19;
  &.active {
    display: block;
    opacity: 1;
  }
}

// container
.container {
  padding-top: 100px;
  &.utility {
    padding-top: 0;
  }
  .content {
    padding-top: 60px;
    padding-bottom: 80px;
    min-height: calc(100vh - 265px);
    &.login {
      padding-top: 21px;
    }
  }
}

// footer
.footer_wrap {
  min-height: 160px;
  padding: 40px 40px 40px 40px;
  background-color: #222;
  font-size: 16px;
  color: #666;
  font-weight: normal;
  text-align: center;
  box-sizing: border-box;
  .footer {
    width: 1080px;
    margin: 0 auto;
  }
  .footer_info {
    p {
      line-height: 1.6;
      span {
        position: relative;
        padding-left: 10px;
        margin-left: 10px;
        em {
          margin-right: 5px;
          font-style: normal;
        }
      }
    }
  }
  p {
    &.copy {
      margin-top: 15px;
    }
  }
}

// content
.content {
  position: relative;
  width: 1080px;
  margin: 0 auto;
  &.wid_content_wrap {
    min-width: 1430px;
    position: relative;
    width: auto;
    padding-left: 40px;
    padding-right: 40px;
    .breadcrumb {
      right: 40px;
    }
    .wid_search_result {
      .tab_wrap {
        min-width: 1430px;
        margin: 30px auto 20px;
      }
    }
    .tbl_col_type {
      min-width: 1430px;
      thead {
        th {
          padding: 0 6px 0 6px;
        }
      }
      tbody {
        td {
          padding: 0 6px 0 6px;
          height: 46px;
          font-size: 15px;
          &.no_data {
            height: 130px;
          }
          &.pd_none {
            padding: 0;
          }
          &.subject {
            text-align: center;
          }
        }
        .tooltip_area {
          margin-left: 0;
          display: block;
        }
        table {
          width: 100%;
          tr {
            &:last-child {
              td {
                border: 0;
              }
            }
          }
        }
        tr {
          &:last-child {
            table {
              td {
                border-color: #ddd;
              }
            }
          }
        }
      }
      .tooltip_area {
        .box_text_tooltip {
          position: absolute;
        }
      }
    }
  }
  .bundle_wrap {
    margin-bottom: 35px;
    .emul_form_wrap {
      min-height: 1120px;
    }
  }
  &.error_page {
    min-height: calc(100vh - 344px);
    padding-bottom: 0;
    padding-top: 80px;
  }
}

// breadcrumb
.breadcrumb {
  position: absolute;
  top: 20px;
  right: 0;
  &::after {
    display: block;
    content: '';
    height: 0;
    font-size: 0;
    opacity: 0;
    filter: alpha(opacity=0);
    clear: both;
  }
  li {
    position: relative;
    float: left;
    padding-left: 12px;
    margin-left: 7px;
    font-size: 14px;
    color: #898989;
    font-weight: normal;
    font-family: 'NotoSansLight';
    &.home {
      padding-left: 0;
      margin-left: 0;
      &::before {
        display: none;
      }
    }
    &::before {
      display: block;
      content: '';
      position: absolute;
      top: 50%;
      left: 0;
      width: 5px;
      height: 10px;
      margin-top: -5px;
      background: url('../images/common/icon_breadcrumb.png') no-repeat 0 50%;
    }
    &:last-child {
      font-family: 'NotoSansMedium';
    }
  }
}

// article
article {
  &.content {
    .title_area {
      padding: 0 0 28px 0;
      &.pdb_no {
        padding: 0 0 0 0;
      }
    }
  }
}

section {
  &.sticky {
    position: sticky;
    top: 120px;
    margin-left: auto;
    float: right;
  }
}

