// bullet list
.bullet_list {
  li {
    position: relative;
    font-size: 16px;
    color: #333;
    font-weight: normal;
    padding-left: 10px;
    line-height: 1.3;
    & + li {
      margin-top: 4px;
    }
    &::before {
      display: block;
      content: '';
      position: absolute;
      top: 10px;
      left: 0;
      width: 5px;
      height: 1px;
      background-color: #333;
    }
  }
  // type 2
  &.type2 {
    li {
      position: relative;
      font-size: 16px;
      color: #666;
      font-weight: normal;
      padding-left: 12px;
      line-height: 1.4;
      font-family: 'NotoSansLight';
      & + li {
        margin-top: 5px;
      }
      &::before {
        display: block;
        content: '';
        position: absolute;
        top: 10px;
        left: 0;
        width: 4px;
        height: 4px;
        background-color: #ec008c;
      }
    }
  }
  &.gray {
    li {
      color: #898989;
      font-family: 'NotoSansLight';
      &::before {
        background-color: #898989;
      }
    }
  }
}

