}

// box_receiver_list
.box_receiver_list {
  border: 1px solid #cccccc;
  height: 200px;
  box-sizing: border-box;
  overflow-y: auto;
  li {
    position: relative;
    display: block;
    border-bottom: 1px solid #f5f5f5;
    height: 40px;
    box-sizing: border-box;
    line-height: 40px;
    padding: 0 20px;
    font-size: 16px;
    color: #898989;
    font-weight: normal;
    cursor: pointer;
    &:hover {
      background-color: #f3f3f3;
    }
    &.selected {
      background-color: #f3f3f3;
      color: #333333;
    }
    .btn_delete {
      display: none;
      position: absolute;
      top: 50%;
      right: 0;
      width: 40px;
      height: 40px;
      transform: translate(0, -50%);
      font-size: 0;
      padding-right: 0;
      &::before,
      &::after {
        position: absolute;
        top: 50%;
        left: 50%;
        width: 22px;
      }
      &::before {
        transform: translate(-50%, -50%) rotate(45deg);
      }
      &::after {
        transform: translate(-50%, -50%) rotate(-45deg);
      }
    }
    &:hover {
      .btn_delete,
      &.selected {
        .btn_delete {
          display: block;
        }
      }
    }
  }
  &::-webkit-scrollbar {
    width: 2px;
  }
  &::-webkit-scrollbar-button:decrement {
    display: block;
    height: 0;
  }
  &::-webkit-scrollbar-button:end:increment {
    display: block;
    height: 0;
  }
