    }
    .box_radio_area {
      margin-right: 20px;
      .custom_radio {
        width: 30px;
        height: 30px;
        box-sizing: border-box;
        label {
          position: relative;
          display: inline-block;
          vertical-align: middle;
          width: 30px;
          height: 30px;
          box-sizing: border-box;
          &::before {
            width: 30px;
            height: 30px;
            box-sizing: border-box;
            -webkit-border-radius: 0;
            -ms-border-radius: 0;
            -moz-border-radius: 0;
            -o-border-radius: 0;
            border-radius: 0;
            border: 1px solid #ddd;
            padding-left: 30px;
          }
          &::after {
            display: block;
            content: '';
            width: 20px;
            height: 20px;
            box-sizing: border-box;
            position: absolute;
            top: 7px;
            left: 6px;
            background: url('../images/common/icon_checkbox_square.png') 0 0 no-repeat;
          }
        }
        input[type='radio'] {
          &:checked {
            & + label {
              &::before {
                border-width: 1px;
                border-color: #222;
              }
              &::after {
                -webkit-border-radius: 0;
                -ms-border-radius: 0;
                -moz-border-radius: 0;
                -o-border-radius: 0;
                border-radius: 0;
                background-position: 0 -20px;
              }
            }
          }
        }
        & + span {
          &.custom_radio {
            margin-left: 6px;
          }
        }
        &.single {
          input[type='radio'] {
            & + label {
              padding-left: 30px;
              width: 30px;
              height: 30px;
              box-sizing: border-box;
            }
          }
        }
        &.double {
          input[type='radio'] {
            & + label {
              &::after {
                background-position: -20px 0;
              }
            }
            &:checked {
              & + label {
                &::after {
                  background-position: -20px -20px;
                }
              }
            }
          }
        }
      }
    }
    .box_input_area {
      display: box;
      display: -webkit-box;
      display: -moz-box;
      display: -webkit-flex;
      display: -ms-flexbox;
      display: -moz-flex;
      display: -o-flex;
      display: flex;
