// emulator
.emulator {
  &_wrap {
    position: sticky;
    top: 120px;
    float: right;
    width: 340px;
    will-change: auto;
    .inner_emul {
      transform: translate3d(0, 0, 0);
      will-change: position, transform;
    }
    .btn_wrap {
      display: box;
      display: -webkit-box;
      display: -moz-box;
      display: -webkit-flex;
      display: -ms-flexbox;
      display: -moz-flex;
      display: -o-flex;
      display: flex;
      justify-content: flex-start;
      align-items: flex-start;
      flex-wrap: wrap;
      margin-top: 20px;
      .btn {
        font-size: 16px;
        &:hover {
          background-color: #fef6fb;
          color: #ec008c;
        }
        &:nth-child(1) {
          flex: 1;
        }
        &:nth-child(2) {
          flex: 1;
        }
        &:nth-child(3) {
          width: 100%;
          margin-top: 8px;
          margin-left: 0;
        }
      }
    }
    .controls {
      text-align: center;
      margin: 15px 0 30px 0;
      & > * {
        vertical-align: middle;
      }
      .pagers {
        display: inline-block;
        font-size: 14px;
        color: #666;
        font-weight: normal;
        span {
          margin: 0 5px 0 5px;
        }
      }
      .btn_prev,
      .btn_next {
        display: inline-block;
        position: relative;
        width: 20px;
        height: 20px;
        font-size: 0;
        &::before {
          display: block;
          content: '';
          position: absolute;
          top: 50%;
          left: 50%;
          width: 5px;
          height: 9px;
          background: url('../images/common/icon_paging.png') repeat-x left center;
          transform: translate(-50%, -50%);
        }
      }
      .btn_prev {
        margin-right: 15px;
      }
      .btn_next {
        margin-left: 15px;
        &::before {
          transform: translate(-50%, -50%) rotate(180deg);
        }
      }
    }
    .guide_info {
      padding: 20px;
      background-color: #f0f0f0;
      .h4_desc {
        position: relative;
        padding-left: 28px;
        font-size: 16px;
        color: #222;
        font-weight: normal;
        font-family: 'NotoSansMedium';
        &::before {
          display: block;
          content: '';
          position: absolute;
          top: 50%;
          left: 0;
          transform: translate(0, -50%);
          width: 20px;
          height: 20px;
          box-sizing: border-box;
          background: url('../images/common/icon_guide_info.png') center center no-repeat;
        }
      }
      .bullet_list {
        li {
          display: box;
          display: -webkit-box;
          display: -moz-box;
          display: -webkit-flex;
          display: -ms-flexbox;
          display: -moz-flex;
          display: -o-flex;
          display: flex;
          align-items: center;
          font-size: 14px;
          color: #666;
          font-weight: normal;
          .h5_desc {
            margin-left: 4px;
          }
        }
      }
    }
  }
  &_area {
    width: 340px;
    border: 1px solid #cccccc;
    border-radius: 40px;
    padding: 9px;
    box-sizing: border-box;
    .emulator_cont {
      position: relative;
      border: 1px solid #cccccc;
      border-radius: 30px;
      background-color: #fafafa;
      overflow: hidden;
      padding: 29px 19px 19px 19px;
      min-height: 580px;
      box-sizing: border-box;
      .view_image {
        margin-bottom: 20px;
      }
      .view_description {
        .text_info {
          font-size: 16px;
          color: #666;
          font-weight: normal;
          line-height: 1.6;
        }
      }
      &.full {
        height: 580px;
        .emulator_view {
          height: 100%;
        }
      }
      &.talk {
        height: 580px;
        background-color: #b6c8d6;
        .emulator_view {
          height: 100%;
        }
      }
    }
    .emulator_view {
      background-color: #ededed;
      -webkit-border-radius: 16px;
      -ms-border-radius: 16px;
      -moz-border-radius: 16px;
      -o-border-radius: 16px;
      border-radius: 16px;
      overflow: hidden;
      .img_area {
        position: relative;
        width: 280px;
        overflow: hidden;
        &.description,
        &.cell {
          height: 50px;
          box-sizing: border-box;
          padding: 0 20px;
          line-height: 50px;
          & > img {
            width: auto;
            height: 30px;
            box-sizing: border-box;
            vertical-align: middle;
          }
        }
        &.carousel {
          width: 248px;
          &.sml {
            width: 120px;
          }
        }
        &.scroll_img_area {
          max-height: 413px;
          box-sizing: border-box;
          overflow-y: auto;
          &::-webkit-scrollbar {
            width: 2px;
          }
          &::-webkit-scrollbar-button:decrement {
            display: block;
            height: 0;
          }
          &::-webkit-scrollbar-button:end:increment {
            display: block;
            height: 0;
          }
          &::-webkit-scrollbar-track {
            background: #dcdcdc;
          }
          &::-webkit-scrollbar-thumb {
            height: 20px;
            width: 2px;
            background: #aaaaaa;
          }
        }
        img {
          width: 100%;
          height: 100%;
        }
        .loading_area {
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
          padding: 10px 10px 10px 10px;
          -webkit-border-radius: 8px;
          -ms-border-radius: 8px;
          -moz-border-radius: 8px;
          -o-border-radius: 8px;
          border-radius: 8px;
          background-color: #fff;
          box-sizing: border-box;
          .loading_img {
            width: 60px;
            margin: 0 auto;
            height: 60px;
            box-sizing: border-box;
            background: url('../images/common/loading.gif') center center no-repeat;
            background-size: cover;
          }
        }
      }
      .img_area_sub_h {
        overflow: hidden;
        img {
          width: 100%;
          height: 100%;
        }
      }
      .img_area_sns {
        position: relative;
        width: 240px;
        border-radius: 8px;
        box-sizing: border-box;
        margin-bottom: 20px;
      }
      .video_area {
        position: relative;
        width: 280px;
        min-height: 110px;
        max-height: 413px;
        overflow: hidden;
        overflow-y: auto;
        box-sizing: border-box;
        background: url('../images/common/video_img_placeholder.png') center center no-repeat;
        img {
          width: 100%;
          height: 100%;
        }
        .video_play {
          position: absolute;
          top: 50%;
          left: 50%;
