// tbl_col_type
.tbl_col_type {
  width: 100%;
  border-collapse: collapse;
  border: 0px;
  border-spacing: 0px;
  thead {
    th {
      height: 48px;
      box-sizing: border-box;
      padding: 0 15px 0 15px;
      font-size: 16px;
      color: #000000;
      font-weight: normal;
      border-top: 1px solid #333;
      border-bottom: 1px solid #aaa;
      text-align: center;
      white-space: nowrap;
      strong {
        font-weight: inherit;
      }
      &.th_line {
        border-left: 1px solid #aaa;
      }
      &[headers] {
        font-size: 16px;
        color: #898989;
        font-weight: normal;
        font-family: 'NotoSansLight';
      }
    }
  }
  tbody {
    td {
      border-bottom: 1px solid #ddd;
      height: 56px;
      box-sizing: border-box;
      padding: 0 8px 0 8px;
      font-size: 16px;
      color: #666666;
      font-weight: normal;
      // white-space: nowrap;
      text-align: center;
      font-family: 'NotoSansLight';
      &.td_line {
        border-left: 1px solid #ddd;
      }
      &.sum {
        background-color: #f8f8f8;
      }
      &.left {
        text-align: left;
      }
			&.img_preview{
				div{
					width: 40px;
					height: 40px;
					margin: 0 auto;
					background: #999999;
				}
			}
    }
    tr {
      td {
        &.no_data {
          height: 130px;
          font-size: 18px;
          color: #898989;
          line-height: 1.5;
        }
      }
      &:hover {
        td {
          background-color: #fafafa;
        }
        .no_data {
          background-color: #fff;
        }
      }
      &:last-child {
        td {
          border-color: #aaa;
          &.td_line {
            border-left: 1px solid #ddd;
          }
        }
      }
      &.write {
        background-color: #f8f8f8;
      }
    }
    .default {
      td {
        background-color: #fafafa;
      }
    }
  }
  .subject {
    text-align: left;
    .ellipsis {
      width: 200px;
    }
  }
  .status {
    &.success {
      color: #333333;
      font-family: 'NotoSansRegular';
    }
  }
  .btn_modify {
    vertical-align: middle;
    display: inline-block;
    width: 40px;
    height: 40px;
    background: url('../images/common/icon_modify.png') no-repeat 50% 50%;
    font-size: 0;
  }
  .btn_delete02 {
    vertical-align: middle;
    display: inline-block;
    width: 40px;
    height: 40px;
    background: url('../images/common/icon_delete.png') no-repeat 50% 50%;
    font-size: 0;
    margin-left: 3px;
  }
  .btn_wrap {
    .btn_text {
      position: relative;
      font-size: 14px;
      color: #666;
      font-weight: normal;
      border: 0;
      padding: 0 0 0 0;
      & + .btn_text {
        padding-left: 9px;
        margin-left: 8px;
        &::before {
          display: block;
          content: '';
          position: absolute;
          top: 50%;
          left: 0;
          width: 1px;
          height: 12px;
          margin-top: -6px;
          background-color: #ddd;
        }
      }
    }
  }
  .label {
    display: inline-block;
    padding: 0 15px;
    height: 24px;
    box-sizing: border-box;
    line-height: 24px;
    background-color: #aa36bb;
    font-size: 14px;
    color: #fff;
    font-weight: normal;
    -webkit-border-radius: 12px;
    -ms-border-radius: 12px;
    -moz-border-radius: 12px;
    -o-border-radius: 12px;
    border-radius: 12px;
    margin-left: 8px;
    &.label_rep {
      background-color: #aa36bb;
    }
  }
  .btn {
    min-width: 0;
  }
  .tooltip_area {
    .box_text_tooltip {
      display: none;
      position: fixed;
      z-index: 5;
      padding: 16px;
      background-color: #686868;
      text-align: left;
      box-sizing: border-box;
      font-size: 14px;
      color: #fff;
      font-weight: normal;
      line-height: 1.3;
      word-break: keep-all;
      -webkit-border-radius: 4px;
      -ms-border-radius: 4px;
      -moz-border-radius: 4px;
      -o-border-radius: 4px;
      border-radius: 4px;
      letter-spacing: -0.03em;
      white-space: nowrap;
      &::before {
        vertical-align: middle;
        display: inline-block;
        content: '';
        position: absolute;
        top: -5px;
        left: 30px;
        z-index: 2;
        width: 10px;
        height: 10px;
        box-sizing: border-box;
        background-color: #686868;
        transform: rotate(45deg);
      }
      &.text_absolute {
        position: absolute;
      }
    }
    &.tooltip_right {
      margin: 0 auto;
      text-align: center;
      .text_info {
        font-size: 15px;
      }
      .box_text_tooltip {
        min-width: 180px;
        right: -70px;
        &::before {
          left: auto;
          right: 123px;
        }
      }
    }
    &.tooltip_L {
      width: 127px;
      .text_info {
        text-align: left;
      }
    }
    &.tooltip_S {
      width: 93px;
      font-size: 15px;
    }
  }
	&.fix_layout{
		table-layout: fixed;
	}
}

// .tbl_row_type
.tbl_row_type {
  width: 100%;
  border-collapse: collapse;
  border: 0px;
  border-spacing: 0px;
  border-top: 1px solid #333;
  tbody {
    th {
      height: 65px;
      box-sizing: border-box;
      padding: 22px 0 24px 30px;
      background-color: #f5f5f5;
      border-bottom: 1px solid #ddd;
      text-align: left;
      font-size: 16px;
      color: #000;
      font-weight: normal;
      font-family: 'NotoSansMedium';
      vertical-align: top;
      letter-spacing: 0;
      .require {
        vertical-align: middle;
        display: inline-block;
        color: #ec008c;
      }
    }
    td {
      height: 65px;
      box-sizing: border-box;
      padding: 12px 50px 12px 30px;
      border-bottom: 1px solid #ddd;
      text-align: left;
      font-size: 16px;
      color: #333;
      font-weight: normal;
      font-family: 'NotoSansLight';
      [class*='custom'] {
        & + .text_desc,
        .btn {
          & + .text_desc {
            margin-left: 12px;
          }
        }
      }
      .text_error {
        margin-top: 10px;
      }
      .title {
        margin-right: 12px;
      }
      .box_add_file {
        .btn + .text_desc {
          margin-left: 0;
        }
      }
    }
    tr {
      &:last-child {
        th,
        td {
          border-bottom: 1px solid #b1b1b1;
        }
      }
    }
  }
  .custom_input {
    min-width: 100px;
    & + .btn {
      margin-left: 10px;
    }
    &.file {
      min-width: 0;
    }
    input {
      &.input_langAuto {
        -webkit-ime-mode: auto;
        -moz-ime-mode: auto;
        -ms-ime-mode: auto;
        ime-mode: auto;
      }
      /*input 기본 값*/
      &.input_langNormal {
        -webkit-ime-mode: normal;
        -moz-ime-mode: normal;
        -ms-ime-mode: normal;
        ime-mode: normal;
      }
      /*input 비활성화, 영문이 기본으로 입력*/
      &.input_lang {
        -webkit-ime-mode: inactive;
        -moz-ime-mode: inactive;
        -ms-ime-mode: inactive;
        ime-mode: inactive;
      }
      /*input 활성화, 기본설정 언어 입력(한글 설정시 한글로 출력) 입력*/
      &.input_langKo {
        -webkit-ime-mode: active;
        -moz-ime-mode: active;
        -ms-ime-mode: active;
        ime-mode: active;
      }
      /*input 한/영 전환키 사용해도 영문 입력*/
      &.input_langEn {
        -webkit-ime-mode: disabled;
        -moz-ime-mode: disabled;
        -ms-ime-mode: disabled;
        ime-mode: disabled;
      }
    }
  }
  .text_value {
    & + .btn {
      margin-left: 30px;
    }
  }
  .btn {
    &.sml {
      border-width: 1px;
      line-height: 28px;
    }
  }
  .box_checkbox {
    vertical-align: middle;
    display: inline-block;
    padding: 20px 20px;
    margin-top: 15px;
    background-color: #f8f8f8;
  }
  .box_add_files {
		& + .btn_delete {
			margin-left: 20px;
		}
    .custom_input {
      width: 450px;
      [readonly] {
        background-color: #fff;
      }
    }
    .btn_add_files {
      font-size: 14px;
      padding: 0 0 0 0;
    }
  }
  &.type2 {
    border-top: 0;
    th {
      background-color: transparent;
      vertical-align: middle;
      font-size: 16px;
      color: #666;
      font-weight: normal;
      font-family: 'NotoSansLight';
      &.th_line {
        border-right: 1px solid #ddd;
      }
      &[scope='rowgroup'] {
        &.th_line {
          font-size: 16px;
          color: #333;
          font-weight: normal;
          font-family: 'NotoSansMedium';
          text-align: center;
          padding-left: 0;
        }
      }
    }
    td {
      font-size: 16px;
      color: #666;
      font-weight: normal;
      font-family: 'NotoSansLight';
    }
    tbody {
      tr {
        &:last-child {
          th,
          td {
            border-color: #ddd;
          }
        }
      }
    }
  }
  &.type3 {
    th {
      padding: 13px 10px 13px 20px;
      line-height: 1.5;
      background: #fff;
    }
    td {
      padding: 13px 10px 13px 10px;
      line-height: 1.5;
      & + td {
        text-align: center;
      }
    }
  }
  &.type4 {
    th {
      vertical-align: middle;
      padding: 13px 10px 13px 20px;
      background: #f0f0f0;
    }
    td {
      padding: 13px 10px 13px 10px;
    }
    tr {
      &:last-child {
        th,
        td {
          border-bottom: 1px solid #000;
        }
      }
    }
  }
}

// .tbl_row_wrap
.tbl_row_wrap {
  position: relative;
  border-top: 1px solid #333;
  &::after {
    display: block;
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 1px;
    border-bottom: 1px solid #aaa;
  }
}

// 브랜드 관리 대량등록
.tblest_type_wrap {
  width: 100%;
  border-collapse: collapse;
  border: 0px;
  border-spacing: 0px;
  border-top: 1px solid #333;
  .tbl_type_list {
    border-bottom: 1px solid #b1b1b1;
    & > li {
      display: table;
      width: 100%;
      border-top: 1px solid #ddd;
      &:first-child {
        border-top: 0;
      }
    }
    .lestL {
      display: table-cell;
      width: 170px;
      padding: 22px 0 24px 30px;
      background: #f5f5f5;
      box-sizing: border-box;
      text-align: left;
      font-size: 16px;
      color: #000;
      font-weight: normal;
      font-family: 'NotoSansMedium';
      vertical-align: top;
      letter-spacing: 0;
    }
    .lestR {
      display: table-cell;
      padding: 12px 50px 12px 30px;
      text-align: left;
      font-size: 16px;
      color: #333;
      font-weight: normal;
      font-family: 'NotoSansLight';
      vertical-align: middle;
    }
    .require {
      vertical-align: middle;
      display: inline-block;
      color: #ec008c;
    }
  }
}

// box_table
.box_table {
  position: relative;
  display: box;
  display: -webkit-box;
  display: -moz-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: -moz-flex;
  display: -o-flex;
  display: flex;
  flex-wrap: wrap;
  border: 1px solid #e0e0e0;
  margin-top: -1px;
  .box_th {
    width: 90px;
    padding: 30px 20px 30px 20px;
    background-color: #f0f0f0;
    border-right: 1px solid #e0e0e0;
    box-sizing: border-box;
    .title {
      position: relative;
      padding-left: 10px;
      font-size: 16px;
      color: #333333;
      font-weight: normal;
      &::before {
        display: block;
        content: '';
        position: absolute;
        top: 12px;
        left: 0;
        width: 3px;
        height: 3px;
        background-color: #333333;
      }
    }
  }
  .box_td {
    width: calc(100% - 90px);
    padding: 20px 20px 20px 20px;
    box-sizing: border-box;
  }
}

// sort_table
.sort_table {
  strong {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    padding-right: 20px;
  }
  .btn_sort {
    display: block;
    position: absolute;
    top: 50%;
    right: 0;
    width: 20px;
    height: 20px;
    font-size: 0;
    margin-top: -10px;
    &::before {
      display: block;
      content: '';
      position: absolute;
      top: 50%;
      right: 0;
      width: 8px;
      height: 12px;
      margin-top: -6px;
      background: url('../images/common/icon_sort.png') no-repeat 50% 50%;
    }
  }
  &.sort_on {
    background-color: #f1dddd;
  }
  &.sort_up {
    .btn_sort {
      &::before {
        transform: rotate(180deg);
      }
    }
  }
}

// tbl_fixed_wrap
.tbl_fixed_wrap {
  position: relative;
  height: 200px;
  box-sizing: border-box;
  padding-top: 48px;
  .tbl_body_scroll {
    overflow-x: hidden;
    overflow-y: auto;
    height: 100%;
    box-sizing: border-box;
    thead {
      th {
        height: 0;
        padding: 0;
      }
    }
    .th_text {
      position: absolute;
      top: 0;
      width: inherit;
      line-height: 48px;
    }
  }
}

// tbl_top_area
.tbl_top_area {
  position: relative;
  margin-bottom: 10px;
  &::after {
    display: block;
    content: '';
    height: 0;
    font-size: 0;
    opacity: 0;
    filter: alpha(opacity=0);
    clear: both;
  }
  .total {
    position: absolute;
    bottom: 0;
    left: 0;
  }
  .board_search_wrap {
    float: right;
  }
  .btn_wrap {
    float: right;
  }
  .desc_list {
    float: left;
    & + .btn_wrap {
      position: absolute;
      bottom: 0;
      right: 0;
    }
  }
}

// each_tb
.each_tb {
  .tbl_col_type {
    border: 1px solid #e0e0e0;
		th {
			background: #f0f0f0;
			border-top: 1px solid #e0e0e0;
			border-bottom: 1px solid #e0e0e0;
		}
		td {
			padding: 0 15px 0 0;
			box-sizing: border-box;
			&:first-child {
				padding-left: 15px;
			}
		}
		tbody{
			tr{
				&:last-child{
					td {
						border-color: #e0e0e0;
					}
				}
			}
		}
		.right {
			text-align: right;
		}
		.custom_checkbox{
			label {
				padding-left: 22px;
			}
		}
  }
	.btn{
		&.btn_member {
			font-size: 0;
			width: 20px;
			height: 20px;
			margin-bottom: 2px;
			border: 0;
			border-radius: 10px;
			&::before {
				display: block;
				content: '';
				position: absolute;
				top: 9px;
				left: 5px;
				z-index: 1;
				width: 10px;
				height: 2px;
				background: #fff;
				box-sizing: border-box;
			}
			&.btn_add_member{
				background: #ec008c;
				&::after {
					display: block;
					content: '';
					position: absolute;
					top: 5px;
					left: 9px;
					z-index: 1;
					width: 2px;
					height: 10px;
					background: #fff;
					box-sizing: border-box;
				}
			}
			&.btn_delete_member {
				background: #bbbbbb;
			}
		}	
	}
}

