}
.tui-grid-layer-selection {
  display: block !important;
  border-style: none !important;
  border-width: 0 !important;
  border-color: none !important;
  border: none !important;
}
.tui-grid-pagination {
  display: box;
  display: -webkit-box;
  display: -moz-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: -moz-flex;
  display: -o-flex;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 20px 0 20px 0 !important;
}
.tui-pagination {
  height: auto !important;
  .tui-first,
  .tui-last,
  .tui-next,
  .tui-next-is-ellip,
  .tui-prev,
  .tui-prev-is-ellip {
    height: auto !important;
    border: none !important;
  }
  .tui-page-btn {
    display: box;
    display: -webkit-box;
    display: -moz-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: -moz-flex;
    display: -o-flex;
    display: flex;
    justify-content: center;
    width: 30px !important;
    height: 30px !important;
    margin: 0 2px 0px 2px !important;
    line-height: 28px !important;
    text-align: center;
    padding: 0 !important;
    font-size: 16px !important;
    color: #666 !important;
    font-weight: normal;
    border: 1px solid #fff !important;
    -webkit-border-radius: 3px;
    -ms-border-radius: 3px;
    -moz-border-radius: 3px;
    -o-border-radius: 3px;
    border-radius: 3px;
    transition: all 0.3s;
    font-family: 'NotoSansLight';
    cursor: pointer !important;
    &.tui-first {
      border: 1px solid #ccc !important;
      background-color: #fff !important;
      border: 1px solid #cccccc !important;
      &:hover {
        border: 1px solid #ccc !important;
        background-color: #fff !important;
        border: 1px solid #cccccc !important;
      }
    }
    &.tui-prev {
      border: 1px solid #ccc !important;
      &:hover {
        border: 1px solid #ccc !important;
        background-color: #fff !important;
        border: 1px solid #cccccc !important;
      }
    }
    &.tui-next {
      border: 1px solid #ccc !important;
      background-color: #fff !important;
      border: 1px solid #cccccc !important;
      &:hover {
        border: 1px solid #ccc !important;
        background-color: #fff !important;
        border: 1px solid #cccccc !important;
      }
    }
    &.tui-last {
      border: 1px solid #ccc !important;
      background-color: #fff !important;
      border: 1px solid #cccccc !important;
      &:hover {
        border: 1px solid #ccc !important;
        background-color: #fff !important;
        border: 1px solid #cccccc !important;
      }
    }
    &:hover,
    &.tui-is-selected {
      border-color: #ec008c !important;
      color: #ec008c !important;
      background-color: #fff !important;
    }
  }
  .tui-ico-first {
    display: block;
    position: absolute;
    top: 50%;
    left: 50%;
    width: 10px !important;
    height: 9px !important;
    background: url('../images/common/icon_paging.png') repeat-x left center !important;
    margin-left: -6px !important;
    margin-top: -4px !important;
    color: #323232;
    font-size: 0;
  }
  .tui-ico-prev {
    display: block;
    position: absolute;
    top: 50%;
    left: 50%;
    width: 5px !important;
    height: 9px !important;
    background: url('../images/common/icon_paging.png') repeat-x left center !important;
    margin-left: -3px !important;
    margin-top: -4px !important;
  }
  .tui-ico-next {
    display: block;
    position: absolute;
    top: 50%;
    left: 50%;
    width: 5px !important;
    height: 9px !important;
    background: url('../images/common/icon_paging.png') repeat-x left center !important;
    margin-left: -2px !important;
    margin-top: -4px !important;
    transform: rotate(180deg) !important;
  }
  .tui-ico-last {
    display: block;
    position: absolute;
    top: 50%;
    left: 50%;
    width: 10px !important;
    height: 9px !important;
    background: url('../images/common/icon_paging.png') repeat-x left center !important;
    margin-left: -4px !important;
    margin-top: -4px !important;
    transform: rotate(180deg) !important;
  }
  .tui-is-selected,
  strong {
    background: none !important;
  }
  .tui-first-child {
    &.tui-is-selected {
      border-left: none;
    }
  }
}
.tui-grid-layer-state {
  p {
    font-size: 18px !important;
    color: #898989 !important;
    line-height: 1.5;
    font-family: 'NotoSansLight' !important;
  }
}
.tui-grid-layer-editing {
  transform: translate(0px, 8px);
  border: 0 solid #ccc;
  background-color: none;
}



