@charset "utf-8";

// style guide
$font-family-base: 'NotoSansRegular', 'Malgun Gothic', Arial, 'Doutm', Tahoma, sans-serif !default;

/*******************************************************************
	Reset
********************************************************************/
html,
body {
  height: 100%;
  box-sizing: border-box;
}
body {
  height: 100%;
  min-height: 100%;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'NotoSansRegular';
  line-height: 1.2;
  letter-spacing: -0.05em;
  font-size: 16px;
  word-break: break-word;
  word-wrap: break-word;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
h1,
h2,
h3,
h4,
h5,
h6,
p,
dl,
dd,
dd {
  margin: 0;
  padding: 0;
}
a {
  color: inherit;
  text-decoration: none;
}
ul,
ol {
  list-style: none;
  margin: 0;
  padding: 0;
}
img,
button {
  border: none;
}
button {
  font: inherit;
}
input:focus,
textarea:focus,
select:focus {
  outline: none;
}
input,
select,
textarea {
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  background-color: transparent;
  font-family: 'NotoSansLight';
  font-size: inherit;
  -webkit-appearance: none;
  border: 0;
  text-shadow: 0;
  resize: none;
}
img {
  max-width: 100%;
}
caption {
  font-size: 0;
  text-indent: -9999px;
}
select::-ms-expand {
  display: none;
  opacity: 0;
}
input::placeholder {
  color: #ccc;
  opacity: 1;
}
input::-ms-clear,
input::-ms-reveal {
  display: none;
}
textarea::placeholder {
  color: #ccc;
  opacity: 1;
  font-size: inherit;
}
table {
  border-spacing: 0;
  border-collapse: collapse;
}
.skip_navi {
  position: absolute;
  top: -30px;
  left: 0;
  z-index: 100;
  width: 100%;
  height: 30px;
  box-sizing: border-box;
  a {
    position: absolute;
    left: 0;
    top: 0;
    display: block;
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    background-color: #222;
    line-height: 30px;
    text-align: center;
    font-size: 13px;
    color: #fff;
    font-weight: normal;
    &:focus {
      top: 30px;
    }
  }
}
.ellipsis {
  display: block;
  width: 100%;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.blind {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  margin: -1px;
  width: 1px;
  height: 1px;
}
.ui_skip {
  position: absolute;
  top: -1px;
  left: -1px;
  width: 0;
  height: 0;
  font-size: 0;
  opacity: 0;
}
